import React from 'react';
import styled from 'styled-components';
import { withPrefix } from 'gatsby';
import Header from '../core/Header';

const Container = styled.main`
  background: ${props => (props.theme === 'dark' ? '#100e12' : '#fff')};
  padding: ${props => (props.isHomepage ? 0 : '50px 25px 50px 25px')};
  color: ${props => (props.theme === 'dark' ? '#fff' : '#2e2f3e')};
`;

const Footer = styled.footer`
  text-align: center;
  width: 100%;
  background-color: #20232f;
  color: #ffffff;
  padding: 15px;
  display: ${props => (props.isHomepage ? 'none' : 'block')};
`;

const Layout = ({ location, children, theme }) => {
  const isHomepage = location.pathname === withPrefix('/');

  return (
    <React.Fragment>
      <Header />
      <Container isHomepage={isHomepage} theme={theme}>
        {children}
      </Container>
      <Footer isHomepage={isHomepage}>

        {new Date().getFullYear()}
        {' '}
Michael Luo
      </Footer>
    </React.Fragment>
  );
};

export default Layout;
